/* -----------------------------------------------------------------------------
  Root (inc)
----------------------------------------------------------------------------- */
body {
  position: relative;
  margin-top: 77px;
  font-family: Manrope, sans-serif;
  font-size: 16px;
  font-weight: 500;
}

.inner-body {
  position: relative;
  width: 100%;
}

h1,
.rubric.h1 {
  display: block;
  width: fit-content;
  margin-top: 1.2rem;
  margin-bottom: 1rem;
  font-size: 34px;
  font-weight: 800;
  letter-spacing: .03em;
}

h2,
.rubric.h2 {
  display: block;
  width: fit-content;
  margin-top: 1.2rem;
  margin-bottom: 1rem;
  font-size: 28px;
  font-weight: 800;
  letter-spacing: .03em;
}

h3,
.rubric.h3 {
  display: block;
  width: fit-content;
  margin-top: 1.2rem;
  margin-bottom: 1rem;
  font-size: 22px;
  font-weight: 800;
  letter-spacing: .03em;
}

h4,
.rubric.h4 {
  display: block;
  width: fit-content;
  margin-top: 1.2rem;
  margin-bottom: 1rem;
  font-size: 18px;
  font-weight: 800;
  letter-spacing: .03em;
}

h5,
.rubric.h5 {
  display: block;
  width: fit-content;
  margin-top: 1.2rem;
  margin-bottom: 1rem;
  font-weight: 800;
  letter-spacing: .03em;
}

h6,
.rubric.h6 {
  display: block;
  width: fit-content;
  margin-top: 1.2rem;
  margin-bottom: 1rem;
  font-weight: 800;
  letter-spacing: .03em;
}

main {
  padding-left: 30px;
  padding-right: 15px;
  min-height: calc(100vh - 77px - 60px - 70px);
}

main img {
  max-width: 100%;
  margin-bottom: 1rem;
}

main p {
  font-family: Manrope, sans-serif;
  font-style: normal;
  letter-spacing: 0.03em;
}

main table {
  margin-bottom: 1rem;
}

main table td,
main table th {
  vertical-align: baseline;
}

.sidebar {
  flex-shrink: 0;
}

.sidebar .sidebar-container {
  top: 0;
  overflow-y: auto;
}

a:active {
  font-weight: 700;
}

th p {
  margin: 0;
  font-weight: inherit;
  font-weight: 500;
}

/* Light theme -------------------------------------------------------------- */
body {
  background-color: #fff;
  color: #000;
}

body a {
  color: #256bd1;
}

body a:hover, body a:focus {
  color: #3585f9;
}

body a:active {
  color: #256bd1;
}

/* Dark- theme -------------------------------------------------------------- */
.dark-theme {
  background-color: #2a2d34;
  color: #fff;
}

.dark-theme a {
  color: #499cfe;
}

.dark-theme a:hover, .dark-theme a:focus {
  color: #6ca2f0;
}

.dark-theme a:active {
  color: #499cfe;
}

/* -----------------------------------------------------------------------------
  Defaults for content (inc)

  General complete style specifically required for the content, including
  lists, tables, etc. that is not necessarily need in all pages.
----------------------------------------------------------------------------- */
/* Fixes the anchor */
.section {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.section::before {
  content: '';
  display: block;
  position: relative;
  width: 0;
  height: 1rem;
  margin-top: -1rem;
}

/* -----------------------------------------------------------------------------
  PART: HEADER

  Style for the header/top bar of the page.
----------------------------------------------------------------------------- */
/* -----------------------------------------------------------------------------
  Component: Brand and logo

  Style for the element containing the logo in the header.
----------------------------------------------------------------------------- */
.navbar-brand {
  display: flex;
  justify-content: flex-end;
}

.navbar-brand .logo-link.web {
  padding-bottom: .3rem;
}

.navbar-brand .logo-link.docs {
  margin-left: 5px;
  line-height: 2.15;
}

.navbar-brand .logo-link.docs:hover {
  text-decoration: none;
}

/* Light theme -------------------------------------------------------------- */
body .navbar-brand .logo-link.docs {
  color: #000;
}

body .navbar-brand .logo-link .logo-light {
  display: inline-block;
}

body .navbar-brand .logo-link .logo-dark {
  display: none;
}

/* Dark- theme -------------------------------------------------------------- */
.dark-theme .navbar-brand .logo-link.docs {
  color: #fff;
}

.dark-theme .navbar-brand .logo-link .logo-light {
  display: none;
}

.dark-theme .navbar-brand .logo-link .logo-dark {
  display: inline-block;
}

/* -----------------------------------------------------------------------------
  Component: Navigation bar

  Style for the menu with links that are external to the documentation (the
  menu in the top bar).
----------------------------------------------------------------------------- */
.navbar-nav .nav-link {
  padding: 0 10px;
  font-size: 18px;
}

.navbar-nav .nav-link:hover, .navbar-nav .nav-link:focus {
  text-decoration: none;
}

.navbar-nav .nav-link:active {
  font-weight: 500;
}

.navbar-nav .nav-link.dropdown-toggle {
  padding-right: 27px;
}

.navbar-nav .nav-link.dropdown-toggle[aria-expanded="true"] {
  text-decoration: none;
}

.navbar-nav .nav-link.dropdown-toggle[aria-expanded="true"]::after {
  transform: rotateX(180deg);
  transition: transform ease-in .1s;
}

.navbar-nav .nav-link.dropdown-toggle::after {
  content: '';
  display: inline-block;
  width: 13px;
  height: 8px;
  background-size: contain;
  background-position: center;
  position: absolute;
  right: 8px;
  bottom: .7rem;
  border: none;
  transition: transform ease-in .1s;
}

.navbar-nav .dropdown-menu {
  min-width: unset;
  padding: 15px;
  margin-top: 20px;
  border: 0;
  border-radius: 10px;
}

.navbar-nav .dropdown-menu::before {
  position: absolute;
  top: -9px;
  width: 18px;
  height: 18px;
  content: "";
  clip-path: inset(-15px 0 0 -15px);
  transform: rotate(45deg);
}

.navbar-nav .dropdown-menu .dropdown-item {
  padding: .25rem .75rem;
}

/* Light theme -------------------------------------------------------------- */
body .navbar-nav {
  background-color: #fff;
}

body .navbar-nav .nav-link {
