/* Dark- theme -------------------------------------------------------------- */
.dark-theme .navbar-nav {
  background-color: #212121;
}

.dark-theme .navbar-nav .nav-link {
  color: #fff;
}

.dark-theme .navbar-nav .nav-link:hover, .dark-theme .navbar-nav .nav-link:focus {
