/* -----------------------------------------------------------------------------
  Component: Version selector

  Style for the release version selector.
----------------------------------------------------------------------------- */
header .tooltip {
  font-family: Manrope, sans-serif;
}

#version-selector.show .dropdown-toggle::after {
  transform: rotate(180deg);
}

#version-selector .dropdown-btn {
  box-sizing: border-box;
  display: flex;
  align-items: center;
  transition: color 0s;
  justify-content: space-between;
  width: calc((385px + 32px) / 2);
  height: 40px;
  border: 2px solid;
  border-radius: 10px;
}

#version-selector .dropdown-btn:focus {
  box-shadow: none;
}

#version-selector .dropdown-menu {
  top: 52px;
  box-sizing: border-box;
  width: 100%;
  border: 2px solid;
  border-radius: 10px;
}

#version-selector .dropdown-menu > li > a {
  display: inline-block;
  width: 100%;
  padding: .16rem .75rem;
  text-decoration: none;
}

#version-selector .dropdown-menu > li > a:hover, #version-selector .dropdown-menu > li > a:focus {
  font-weight: 700;
}

#version-selector .dropdown-menu > li > a.disabled {
  font-weight: 500;
}

#version-selector .dropdown-menu > li > a.disabled:hover, #version-selector .dropdown-menu > li > a.disabled:focus {
  font-weight: 500;
}

/* Light theme -------------------------------------------------------------- */
body header .tooltip .arrow::before {
  border-left-color: #e2eefe;
}

body header .tooltip .tooltip-inner {
  color: #000;
  background-color: #e2eefe;
}

body #version-selector .dropdown-btn {
  background-color: #fff;
  border-color: #fedd0b;
  color: #000;
}

body #version-selector .dropdown-btn[aria-expanded="false"]:focus {
  box-shadow: inset 0 0 2px 2px rgba(101, 124, 151, 0.2);
}

body #version-selector .dropdown-menu {
  background-color: #fff;
  border-color: #fedd0b;
}

body #version-selector .dropdown-menu > li > a {
  color: #000;
}

body #version-selector .dropdown-menu > li > a:hover, body #version-selector .dropdown-menu > li > a:focus {
  background-color: #e2eefe;
}

body #version-selector .dropdown-menu > li > a.disabled {
  color: #b7c6d7;
}

/* Dark- theme -------------------------------------------------------------- */
.dark-theme header .tooltip .arrow::before {
  border-left-color: #242424;
}

.dark-theme header .tooltip .tooltip-inner {
  color: #fff;
  background-color: #242424;
}

.dark-theme #version-selector .dropdown-btn {
  background-color: #2a2d34;
  border-color: #ffe853;
  color: #fff;
}

.dark-theme #version-selector .dropdown-btn[aria-expanded="false"]:focus {
  box-shadow: inset 0 0 2px 2px rgba(152, 162, 179, 0.5);
}

.dark-theme #version-selector .dropdown-menu {
  background-color: #2a2d34;
  border-color: #ffe853;
}

.dark-theme #version-selector .dropdown-menu > li > a {
  color: #fff;
}

.dark-theme #version-selector .dropdown-menu > li > a:hover, .dark-theme #version-selector .dropdown-menu > li > a:focus {
  background-color: #242424;
}

.dark-theme #version-selector .dropdown-menu > li > a.disabled {
  color: #a6b5c8;
}

header {
