/* -----------------------------------------------------------------------------
  Component: Search box

  Style for the searchbox in general.
----------------------------------------------------------------------------- */
.search-box {
  border: 2px solid;
  border-radius: 10px;
}

.search-box .search-form {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  height: 46px;
}

.search-box .search-form .form-control {
  position: relative;
  z-index: 1;
  width: 100%;
  font-size: 18px;
  font-weight: 400;
  border: none;
}

.search-box .search-form .btn-search {
  position: absolute;
  right: 0;
  z-index: 10;
  width: 46px;
  height: 46px;
  padding: 5px;
  background: url("../../images/icons/svg-icons.svg#search");
  background-size: 41px 41px;
  background-position: center;
  border: none;
}

/* Light theme -------------------------------------------------------------- */
body .search-box {
  background-color: #ebf2fb;
  border-color: #c1ccd9;
}

body .search-box .form-control {
  background-color: #ebf2fb;
}

body .search-box ::placeholder {
  color: #a6b5c8;
}

/* Dark- theme -------------------------------------------------------------- */
.dark-theme .search-box {
  background-color: #343741;
  border-color: #fff;
}

.dark-theme .search-box .form-control {
  background-color: #343741;
}

.dark-theme .search-box ::placeholder {
  color: #a6b5c8;
}

/* -----------------------------------------------------------------------------
  Component: Global TOC

  Style for the menu containing the full index of the documentation release
  folder.
  Note: the elements of the global TOC are automatically generated by the html
  builder.
----------------------------------------------------------------------------- */
#global-toc ul {
  padding: 0;
  list-style: none;
}

#global-toc ul .current-toc-node:hover .toc-toggle-btn {
  display: block;
}

#global-toc ul .current-toc-node::before {
  position: absolute;
  top: 1.1rem;
  display: inline-block;
  width: 6px;
  height: 6px;
  pointer-events: none;
  content: "";
  border: 5px solid;
  border-right-color: transparent;
  border-bottom: 0;
  border-left-color: transparent;
  transform: rotate(-90deg);
}

#global-toc .show > ul {
  display: block;
}

#global-toc li > ul {
  display: none;
}

#global-toc a {
  position: relative;
  display: inline-block;
  width: 100%;
  padding: 5px 0;
  font-size: 18px;
  font-weight: 400;
  line-height: 32px;
  text-decoration: none;
}

#global-toc a:hover .toc-toggle-btn {
  display: block;
}

#global-toc .current .toctree-l2 > a {
  padding-left: 20px;
}

#global-toc .current .toctree-l2 > a.current-toc-node::before {
  left: 5px;
}

#global-toc .current .toctree-l3 > a {
  padding-left: 40px;
}

#global-toc .current .toctree-l3 > a.current-toc-node::before {
  left: 25px;
}

#global-toc .current .toctree-l4 > a {
  padding-left: 60px;
}

#global-toc .current .toctree-l4 > a.current-toc-node::before {
  left: 45px;
}

#global-toc .current .toctree-l5 > a {
  padding-left: 80px;
}

#global-toc .current .toctree-l5 > a.current-toc-node::before {
  left: 65px;
}

#global-toc .current .toctree-l6 > a {
  padding-left: 100px;
}

#global-toc .current .toctree-l6 > a.current-toc-node::before {
  left: 85px;
}

#global-toc [class*="toctree-l"].current > a {
  font-weight: 700;
}

#global-toc [class*="toctree-l"].current.show > ul {
  display: block;
}

#global-toc [class*="toctree-l"].show > a .toc-toggle-btn {
  transform: rotateZ(180deg);
}

#global-toc [class*="toctree-l"] > ul {
  display: none;
}

#global-toc .toctree-l1 > a {
  padding-left: 40px;
}

#global-toc .toctree-l1 > a::before {
  left: 25px;
}

#global-toc .toctree-l1 > ul {
  margin-left: 40px;
  border-left: 1px solid;
}

#global-toc .toc-toggle-btn {
  display: none;
  position: absolute;
  top: 0;
  right: 32px;
  width: 42px;
  height: 42px;
  background-color: transparent;
  background-size: 17px 10px;
  background-position: center;
  background-repeat: no-repeat;
  border: none;
}

/* Light theme -------------------------------------------------------------- */
body #global-toc ul .current-toc-node {
  color: #256bd1;
}

body #global-toc ul .current-toc-node:hover {
  color: #3585f9;
}

body #global-toc ul .current-toc-node:hover .toc-toggle-btn {
