/* -----------------------------------------------------------------------------
  PART: RIGHT SIDEBAR

  Style for the left sidebar, which contains the local TOC.
----------------------------------------------------------------------------- */
/* -----------------------------------------------------------------------------
  Component: Mode switch

  Style for the component that switches the theme style to light or dark mmode.
----------------------------------------------------------------------------- */
#light-dark-switch {
  display: flex;
  flex-direction: row;
  padding-bottom: 8px;
}

#light-dark-switch > span:first-child {
  margin-left: 8px;
}

#light-dark-switch .switch-icon {
  width: 20px;
  height: 20px;
  margin-left: 5px;
  margin-right: 5px;
  display: inline-block;
  background-size: contain;
  background-position: center;
}

#mode-switch {
  box-sizing: content-box;
  position: relative;
  top: 1px;
  display: flex;
  flex-direction: row;
  width: 26px;
  height: 16px;
  padding: 0;
  margin: 0 5px;
  border: 1px solid;
  border-radius: 25px;
}

#mode-switch.dark {
  justify-content: flex-end;
}

#mode-switch.dark span {
  left: calc(100% - 16px);
}

#mode-switch span {
  position: absolute;
  left: 0;
  display: inline-block;
  width: 16px;
  height: 16px;
  content: "";
  border-radius: 50%;
  transition: left ease-in .075s;
  background-color: #fff;
}

/* Light theme -------------------------------------------------------------- */
body #light-dark-switch {
  color: #000;
}

body #light-dark-switch .switch-icon.icon-sun {
  background: url("../../images/icons/svg-icons.svg#sun-lt");
}

body #light-dark-switch .switch-icon.icon-moon {
  background: url("../../images/icons/svg-icons.svg#moon-lt");
}

body #mode-switch {
  background-color: #3585f9;
  border-color: #3585f9;
}

/* Dark- theme -------------------------------------------------------------- */
.dark-theme #light-dark-switch {
  color: #fff;
}

.dark-theme #light-dark-switch .switch-icon.icon-sun {
  background: url("../../images/icons/svg-icons.svg#sun-dt");
}

.dark-theme #light-dark-switch .switch-icon.icon-moon {
  background: url("../../images/icons/svg-icons.svg#moon-dt");
}

.dark-theme #mode-switch {
  background-color: #499cfe;
  border-color: #499cfe;
}

/* -----------------------------------------------------------------------------
  Component: Local TOC

  Style for the menu containing the index of the current page.
  Note: the elements of the local TOC are automatically generated by the html
  builder.
----------------------------------------------------------------------------- */
#local-toc {
  padding: 0;
}

#local-toc > ul > li > ul > li > a {
  padding-left: 20px;
}

#local-toc > ul > li > ul > li > ul > li > a {
  padding-left: 30px;
}

#local-toc > ul > li > ul > li > ul > li > ul > li > a {
  padding-left: 40px;
}

#local-toc > ul > li > ul > li > ul > li > ul > li > ul > li > a {
  padding-left: 50px;
}

#local-toc > ul > li > ul > li > ul > li > ul > li > ul > li > ul > li > a {
  padding-left: 60px;
}

#local-toc > ul > li > ul > li > ul > li > ul > li > ul > li > ul > li > ul > li > a {
  padding-left: 70px;
}

#local-toc .toc-title {
  display: inline-block;
  margin-top: 20px;
  height: 30px;
  font-size: 18px;
  font-weight: 700;
  text-transform: uppercase;
}

#local-toc > ul {
  height: calc(100vh - 77px - 60px - 40px - 30px - 30px - 70px);
  overflow-y: auto;
}

#local-toc ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

#local-toc ul a {
  font-size: 14px;
  border-left: 2px solid;
  line-height: 24px;
  padding-top: 4px;
  padding-bottom: 4px;
}

#local-toc ul a:hover, #local-toc ul a:focus {
  outline: none;
}

#local-toc ul a:active {
  font-weight: 700;
